@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PTMono", "Courier New", monospace;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f2f5f6;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f2f5f6;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #f2f5f6;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  text-align: center;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn--main-header {
  display: block;
  height: 40px;
  min-width: 164px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  padding-top: 5px;
  padding-right: 15px;
  padding-left: 20px;
  line-height: 16px;
  color: #f2f5f6;
  letter-spacing: 0.05em;
  margin-right: 65px;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #f2f5f6;
  border-radius: 3px;
}

@media (max-width: 1023px) {
  .btn--main-header {
    display: none;
  }
}
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  height: 610;
  padding: 40px;
  padding-top: 33px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 12px;
  right: 17px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  fill: #ffffff;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.modal__agree {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.modal__button {
  width: 100%;
  background-color: #041827;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  display: block;
  text-align: center;
  padding: 15px 25px;
  margin-top: 24px;
  margin-bottom: 22px;
  letter-spacing: 0.05em;
}

.modal__agree input[type=checkbox]:focus-within ~ .question__control-mark {
  outline: 1px solid #ffffff;
}

.modal__agree::before {
  display: none;
}

.modal__agree-wrapper {
  display: flex;
  flex-direction: column-reverse;
}

.modal__content .question__control-mark {
  top: 0;
}

.main-header__phone-text,
.main-header__email-text {
  display: block;
  color: #f2f5f6;
  font-family: "PTMono", "Courier New", monospace;
  margin: 0;
  margin-left: 16px;
}

.main-header {
  padding-top: 25px;
  padding-bottom: 35px;
  z-index: 1;
  width: 100%;
  background: linear-gradient(to bottom, #041827 85%, rgba(255, 255, 255, 0) 100%);
  position: fixed;
}

.main-header__wrapper {
  padding: 0 14px 0 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-header__logo {
  padding-top: 2px;
}

.main-header__logo-link {
  width: 100%;
  height: 55px;
}

.main-header__link {
  padding: 0;
  display: flex;
  align-items: center;
}
.main-header__link:hover {
  opacity: 0.8;
}

.main-header__contacts {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 125px;
}

.main-header__logo-link:hover {
  opacity: 0.8;
}

.main-header__logo--mobile {
  min-width: 215px;
}

@media (max-width: 1023px) {
  .main-header__contacts {
    column-gap: 0;
  }
  .main-header__wrapper {
    padding: 0;
    justify-content: flex-end;
    align-items: flex-start;
  }
  .main-header {
    padding: 20px 50px 40px;
    position: inherit;
  }
  .main-header__logo {
    padding: 0;
    margin-right: auto;
  }
  .main-header__logo-image {
    width: 55px;
    height: 55px;
    margin-right: 15px;
  }
  .main-header__link {
    padding: 10px;
  }
  .main-header__logo-link {
    display: flex;
  }
  .main-header__phone {
    padding-left: 20px;
    margin-right: 19px;
  }
  .main-header__email-text {
    display: none;
  }
  .main-header__phone-text {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding: 10px 0 20px 0;
  }
  .main-header__logo {
    margin-top: 2px;
  }
  .main-header__wrapper {
    padding: 0 35px 0 26px;
  }
  .main-header__phone {
    margin-right: 0;
  }
  .main-header__logo--mobile {
    min-width: 35px;
  }
}
.main-footer {
  background: #041827;
  color: #ffffff;
}

.main-footer__wrapper {
  display: grid;
  grid-template-columns: auto 38.8% 28.5%;
  column-gap: 24px;
  padding: 51px 19px 65px;
}

.main-footer__logo-link {
  display: flex;
  align-items: center;
}
.main-footer__logo-link:hover {
  opacity: 0.8;
}

.main-footer__logo-social {
  flex-direction: column;
  min-width: 215px;
}

.main-footer__title-wrapper {
  color: #f2f5f6;
}

.main-footer__title {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

.main-footer__title-small {
  margin: 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  font-family: "Roboto", "Arial", sans-serif;
}

@media (max-width: 1023px) {
  .main-footer {
    padding: 41px 0 40px;
  }
  .main-footer__wrapper {
    grid-template-columns: 48% 48%;
    column-gap: 30px;
  }
  .main-footer__logo-social {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    grid-column: 1/-1;
  }
}
@media (max-width: 767px) {
  .main-footer {
    padding-top: 35px;
  }
  .main-footer__wrapper {
    display: block;
  }
  .main-footer__logo-social {
    flex-direction: column;
  }
}
.social__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 30px 10px 23px;
  margin: 0;
}

.social__item {
  margin-left: 12px;
}

.social__item:first-child {
  margin-left: 0;
}

.social__link {
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
}
.social__link:hover {
  opacity: 0.8;
}

@media (max-width: 1023px) {
  .social__list {
    padding-top: 14px;
    padding-right: 4px;
  }
  .social__link:last-child {
    padding-right: 0;
    padding-left: 12px;
  }
}
@media (max-width: 767px) {
  .social__item {
    margin-left: 15px;
  }
  .social__list {
    padding-top: 15px;
    padding-right: 17px;
  }
}
.footer-navigation {
  margin-left: 4px;
}

.footer-navigation:last-child::before {
  display: none;
}

.footer-navigation__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  margin: 0;
  letter-spacing: 0.05em;
  position: relative;
  padding: 7px 0 23px;
}
.footer-navigation__title--active::before {
  display: none;
}
.footer-navigation__title--active:focus-visible {
  outline: none;
}

.footer-navigation__item {
  font-size: 16px;
  line-height: 21px;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 9px;
}

.footer-navigation__item:last-child {
  padding-bottom: 21px;
}

.footer-navigation__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  transition: 0.3s;
  overflow: hidden;
  color: rgba(255, 255, 255, 0.7);
}

.footer-navigation__list--site {
  column-count: 2;
  display: block;
}

.footer-navigation__link,
.footer-navigation__list-link {
  color: rgba(255, 255, 255, 0.7);
  font-family: "PTMono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
}
.footer-navigation__link:hover,
.footer-navigation__list-link:hover {
  opacity: 0.8;
}

@media (max-width: 1023px) {
  .footer-navigation {
    grid-template-columns: 220px 320px;
    gap: 22px;
  }
  .footer-navigation__list {
    width: 100%;
  }
  .footer-navigation__list--site {
    display: contents;
  }
  .footer-navigation,
.footer-navigation:last-child {
    margin-left: 0;
  }
  .footer-navigation__list-wrapper {
    display: block;
  }
  .footer-navigation__title {
    padding-top: 24px;
  }
  .footer-navigation__item:last-child {
    padding-bottom: 0;
  }
}
@media (max-width: 767px) {
  .footer-navigation {
    position: relative;
    display: block;
    width: 100%;
  }
  .footer-navigation::before {
    position: absolute;
    top: 0;
    left: -25px;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
  .footer-navigation::after {
    position: absolute;
    bottom: 0;
    left: -25px;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
  .footer-navigation:last-child::before {
    display: none;
  }
  .footer-navigation__item:last-child {
    margin-bottom: 17px;
  }
  .footer-navigation__title {
    padding: 23px 4px 23px;
  }
  .footer-navigation__title::after, .footer-navigation__title::before {
    position: absolute;
    display: block;
    content: "";
    right: 6px;
    top: 29px;
    width: 15px;
    height: 1px;
    background-color: #c4c4c4;
  }
  .footer-navigation__title::before {
