.question {
  position: relative;
  color: #ffffff;
  background-color: #00b2ff;
}
.question::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(4, 24, 39, 0.5);
  clip-path: polygon(0 100%, 0 0, 51% 0);
}

