.question__button {
  background-color: #041827;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  display: block;
  text-align: center;
  padding: 15px 25px;
  letter-spacing: 0.05em;
  margin-right: 23px;
  width: 100%;
  grid-column: 1;
  grid-row: 1;
}

.question__label-checkbox {
  font-family: "PTMono", "Courier New", monospace;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  position: relative;
  cursor: pointer;
  color: #ffffff;
}

.question__label-checkbox:last-child {
  margin-left: 22px;
}

.question__agree {
  display: flex;
  align-items: flex-end;
  grid-column: 2;
}

.question__agree input[type=checkbox]:focus-within ~ .question__control-mark {
  outline: 1px solid #ffffff;
}

.question__agree-wrapper {
  display: grid;
  margin-top: 28px;
  grid-template-columns: 1fr 1fr;
  gap: 57px;
}

.question__control-mark {
  width: 15px;
  height: 14px;
  position: absolute;
  border: 1px solid #ffffff;
  border-radius: 2px;
  left: -26px;
  top: 7px;
  cursor: pointer;
}

.question input[type=checkbox]:checked + .question__control-mark::before {
  position: absolute;
  width: 7px;
  height: 7px;
  content: "";
  background-image: url("../img/svg/checked.svg");
  background-repeat: no-repeat;
  top: 2px;
  background-position: center;
  right: 3px;
}

