    bottom: 11px;
  }
  .question__button {
    width: 266px;
    padding-bottom: 10px;
  }
  .question__form-wrapper {
    width: auto;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .question__wrapper {
    padding: 47px 25px 88px;
  }
  .question h2 {
    font-size: 30px;
    line-height: 42px;
  }
  .question__button {
    width: 100%;
    margin: 24px 0 19px;
  }
  .question__agree-wrapper {
    display: flex;
    flex-direction: column-reverse;
    gap: 0;
    margin: 0;
  }
  .question__label-checkbox:last-child {
    margin-left: 30px;
  }
}
.form {
  color: #ffffff;
}

form ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 47% 48%;
  gap: 30px;
}

.form p {
  font-family: "PTMono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 35px;
  margin-top: 9px;
}

.form h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 50px;
  color: #f2f5f6;
  margin: 0;
  margin-bottom: 15px;
  display: block;
}

.form__item input {
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 15px;
  border-radius: 3px;
  margin-bottom: 20px;
  width: 100%;
  cursor: text;
}

.form__wrapper input {
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 15px;
  border-radius: 3px;
  margin-bottom: 20px;
  width: 100%;
}

.form ::placeholder {
  color: #ffffff;
  font-family: "PTMono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  border: none;
}

.form textarea {
  width: 100%;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  padding: 25px 15px;
  min-height: 156px;
}

.form__content h2 {
  font-size: 30px;
  line-height: 42px;
  margin-bottom: 4px;
}

.form__content p {
  margin-top: 0;
}

@media (max-width: 1023px) {
  .form ul {
    gap: 36px;
  }
  .form p {
    margin-bottom: 23px;
    margin-top: 6px;
  }
  .form__item input {
    padding-bottom: 18px;
  }
  .form textarea {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  form ul {
    display: block;
  }
  .form p {
    font-family: "PTMono", "Courier New", monospace;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 35px;
    margin-top: 7px;
  }
  .form__item input {
    padding-bottom: 16px;
  }
  .form textarea {
    min-height: 180px;
    padding-top: 23px;
  }
}
